/* Global */
:root {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.content-box-component {
  box-sizing: content-box;
}

html {
  height: 100%;
  font-family: "UniNeue", Arial, sans-serif;
  font-size: 18px;
  line-height: 1.4;
}
@media (max-width: 480px) {
  html {
    font-size: 16px;
  }
}

body {
  height: 100%;
  background: url("../img/bg-el1.webp") 300px -100px, url("../img/bg-el2.webp") -300px -164px, linear-gradient(to right, #0A0A0A 0% 70%, #050505 90% 100%);
  background-color: #121418;
  background-repeat: no-repeat;
  color: #FFFFFF;
  animation: swim 10s linear infinite alternate;
}

@keyframes swim {
  0% {
    background-position: 300px -100px, -300px -164px, 0 0;
  }
  15% {
    background-position: 295px -96px, -295px -160px, 0 0;
  }
  30% {
    background-position: 290px -93px, -297px -155px, 0 0;
  }
  45% {
    background-position: 291px -87px, -292px -155px, 0 0;
  }
  60% {
    background-position: 287px -84px, -290px -151px, 0 0;
  }
  75% {
    background-position: 285px -87px, -295px -145px, 0 0;
  }
  90% {
    background-position: 283px -82px, -290px -142px, 0 0;
  }
  100% {
    background-position: 283px -77px, -287px -140px, 0 0;
  }
}
button,
input {
  outline: none;
  border: none;
  cursor: pointer;
}

fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
}

ul li,
ol li {
  list-style: none;
}

dd {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  position: relative;
  display: grid;
  grid-template-rows: 1fr min-content;
  min-height: 100%;
  min-width: 320px;
  overflow: hidden;
  background-image: url("../img/cover.webp");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: contain;
}

.container {
  margin: 0 auto;
  max-width: 1105px;
  padding: 0 20px;
}
.container_p {
  padding: 0;
}
@media (min-width: 1145px) {
  .container {
    padding: 0;
  }
}

.no-js .wrapper::before {
  content: "Пожалуйста, разрешите использовать JavaScript! Без него сайт грустит...";
  position: fixed;
  top: -300px;
  left: 0;
  width: 100%;
  padding: 20px;
  z-index: 99999;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.1rem;
  font-weight: 500;
  color: #f1535e;
  background-color: rgba(0, 0, 0, 0.75);
  animation-name: real;
  animation-delay: 3s;
  animation-duration: 7s;
  animation-fill-mode: forwards;
}

@keyframes real {
  0% {
    top: -300px;
  }
  10% {
    top: 0;
  }
  90% {
    top: 0;
  }
  100% {
    top: -300px;
  }
}
.title {
  color: #FCFCFC;
  font-family: "UniSans", Arial, sans-serif;
  font-size: 38px;
  line-height: 45.6px;
}
@media (max-width: 480px) {
  .title {
    font-size: 32px;
  }
}

.header {
  padding-top: 25px;
  margin-bottom: 100px;
}

@media (max-width: 900px) {
  .header_fixed {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    padding-bottom: 20px;
    z-index: 100;
    background-color: rgba(0, 0, 0, 0.8);
  }
}

@media (max-width: 480px) {
  .header {
    margin-bottom: 50px;
    padding: 10px 0;
  }
}

.header__container {
  display: flex;
  align-items: center;
}

.header__logo-block {
  margin-right: 21px;
}
@media (max-width: 480px) {
  .header__logo-block {
    width: 120px;
  }
}

@media (max-width: 900px) {
  .header__nav-block {
    position: fixed;
    z-index: 50;
    top: 0;
    right: -200%;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 320px;
    padding: 60px 30px 30px 30px;
    background-color: rgba(17, 19, 23, 0.9);
