  .header__nav {
    justify-content: flex-start;
  }
}
@media (max-width: 900px) {
  .header__nav {
    flex-direction: column;
    align-items: center;
    margin-right: 0;
  }
}

@media (max-width: 1145px) {
  .nav__item {
