.header__desc {
  max-width: 180px;
  margin-right: 65px;
  font-family: "UniNeue", Arial, sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 1.3;
}
@media (max-width: 1145px) {
  .header__desc {
    display: none;
  }
}

.header__contact {
  text-align: right;
}
@media (max-width: 900px) {
  .header__contact {
    display: none;
  }
}

.header__phone {
  display: block;
  font-family: "UniNeue", Arial, sans-serif;
  font-weight: 700;
  font-size: 22px;
}

.header__link {
  padding-bottom: 3px;
  font-family: "UniNeue", Arial, sans-serif;
  font-weight: 400;
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 0.6px;
  color: #F8B432;
  border-bottom: 2px dashed #6E5C02;
}

.header__humb {
  position: relative;
  display: none;
  width: 50px;
  height: 35px;
  margin-left: auto;
  margin-right: 30px;
  padding: 0;
  background-color: transparent;
}
.header__humb::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0 17px rgba(255, 255, 255, 0.9), 0 33px rgba(255, 255, 255, 0.9);
  transition: all 0.5s;
}
.header__humb:hover::before {
  background-color: white;
  box-shadow: 0 17px white, 0 33px white;
}
@media (max-width: 900px) {
  .header__humb {
    display: block;
  }
}
@media (max-width: 480px) {
  .header__humb {
    width: 35px;
    height: 28px;
    margin-right: 5px;
  }
  .header__humb::before {
    height: 2px;
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: 0 13px rgba(255, 255, 255, 0.9), 0 26px rgba(255, 255, 255, 0.9);
  }
  .header__humb:hover::before {
    background-color: white;
    box-shadow: 0 13px white, 0 26px white;
  }
}

.nav__close {
  position: relative;
  display: none;
  width: 60px;
  height: 60px;
  background-color: transparent;
}
.nav__close::before, .nav__close::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 30px;
  height: 2px;
  background-color: #ffffff;
}
.nav__close::before {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.nav__close::after {
  transform: translate(-50%, -50%) rotate(45deg);
}
@media (max-width: 900px) {
  .nav__close {
    position: absolute;
    top: 0px;
    right: 0px;
    display: block;
  }
}

.header__mini-desc {
