  .header_fixed + .hero {
    margin-top: 130px;
  }
}

.hero__container {
  position: relative;
}
.hero__container .hero__before {
  position: absolute;
  top: 0;
  left: -620px;
  width: 580px;
  height: 580px;
  transform: translateX(-100%);
  background-image: url("../img/tri1.webp");
  background-size: contain;
}
.hero__container .hero__after {
  position: absolute;
  top: -100px;
  right: -650px;
  width: 550px;
  height: 550px;
  transform: translateX(100%) rotate(45deg);
  background-image: url("../img/tri1.webp");
  background-size: contain;
}
@media (max-width: 1600px) {
  .hero__container .hero__before {
    top: -100px;
    left: -520px;
  }
  .hero__container .hero__after {
    top: -200px;
    right: -600px;
  }
}

.hero__looseer {
  position: absolute;
  right: -200px;
  top: -75px;
}
.hero__looseer:after {
  content: "";
  position: absolute;
  z-index: 1;
  top: 90%;
  right: -100px;
  width: 964px;
  height: 262px;
  background: #111215;
  opacity: 0.9;
  filter: blur(80px);
}
@media (max-width: 1145px) {
  .hero__looseer {
    display: none;
  }
