.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  left: -10000px;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.5);
  transition: opacity 0.5s;
}

.modal_active {
  left: 0;
  opacity: 1;
}

.modal__box {
  position: relative;
  max-width: 400px;
  border-radius: 10px;
  padding: 60px 40px;
  background-color: #ffffff;
  color: #000000;
  text-align: center;
}
.modal__box .talk__input-block {
  display: inline-block;
  padding: 0;
}
