  padding: 0 20px;
}
@media (max-width: 480px) {
  .modal__box {
    padding: 50px 20px;
  }
}

.modal__close {
  position: absolute;
  top: 0;
  right: 0;
  width: 60px;
  height: 60px;
  background-color: transparent;
}
.modal__close::after, .modal__close::before {
  content: "";
  position: absolute;
  top: 30px;
  right: 0;
  width: 30px;
  height: 2px;
  background-color: rgba(0, 0, 0, 0.6);
}
.modal__close::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.modal__close::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.modal__title {
  margin-bottom: 40px;
  font-size: 32px;
  text-align: center;
  font-family: "UniNeue", Arial, sans-serif;
  font-weight: 700;
}